body {
  background-color: #444;
  color: white;
  text-align: center;
  font-size: 20px;
  font-family: 'Quicksand';
  padding-top: 5%;
  word-wrap: break-word;
}

.logo {
  width: 250px;
  height: 250px;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.WalletInfo {
  width: 500px;
}

.Block, .ConductTransaction, .TransactionPool {
  border: 1px solid white;
  padding: 5%;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 60vw;
}

.Transaction {
  padding: 5%;
}

a, a:hover {
  color: rgba(102, 188, 238, 0.4);
  text-decoration: underline;
}

